<template>
  <div class="wrong-questions-page">
    <section v-if="!hasBank" class="empty card">
      <van-empty image="folder" description="请先导入题库">
        <van-button size="small" type="primary" @click="$router.push('/import')">
          去导入
        </van-button>
      </van-empty>
    </section>

    <template v-else>
      <section v-if="wrongQuestions.length === 0" class="empty card">
        <van-empty image="success" description="太棒了！目前没有错题">
          <van-button size="small" type="primary" @click="$router.push('/quiz')">
            去闯关
          </van-button>
        </van-empty>
      </section>

      <template v-else>
        <section class="card wrong-summary">
          <div class="summary-content">
            <div class="summary-item">
              <p class="summary-value">{{ wrongQuestions.length }}</p>
              <p class="summary-label">错题数量</p>
            </div>
            <van-button
              type="primary"
              size="small"
              round
              @click="startPractice"
            >
              开始练习
            </van-button>
          </div>
        </section>

        <section class="wrong-list">
          <div
            v-for="(question, index) in wrongQuestions"
            :key="question.id || index"
            class="wrong-item card"
            @click="viewQuestion(question)"
          >
            <div class="wrong-item__header">
              <van-tag :type="getTypeTag(question.type)" round plain>
                {{ getTypeLabel(question.type) }}
              </van-tag>
              <span class="wrong-item__id">ID：{{ question.id || question.I }}</span>
            </div>
            <p class="wrong-item__question">{{ question.question }}</p>
            <div class="wrong-item__meta">
              <span class="wrong-item__answer">
                正确答案：{{ formatAnswer(question) }}
              </span>
            </div>
          </div>
        </section>
      </template>
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import { formatMultipleAnswer } from '@/utils/helper';

export default {
  name: 'WrongQuestionsPage',
  computed: {
    ...mapGetters('questions', ['hasBank']),
    ...mapState('questions', ['wrongQuestions']),
  },
  mounted() {
    this.loadWrongQuestions();
  },
  methods: {
    ...mapActions('questions', ['loadWrongQuestions', 'loadWrongQuestionsForPractice']),
    getTypeLabel(type) {
      if (type === 'multiple') return '多选';
      if (type === 'judge') return '判断';
      return '单选';
    },
    getTypeTag(type) {
      if (type === 'multiple') return 'warning';
      if (type === 'judge') return 'default';
      return 'primary';
    },
    formatAnswer(question) {
      const answer = question.correctAnswer;
      if (Array.isArray(answer)) {
        return formatMultipleAnswer(answer);
      }
      return answer || '-';
    },
    viewQuestion(question) {
      let options = ''
      question.options.map(option => {
        options += `<p style="font-weight: 600;"><span class="option-key">${option.key}.</span> <span class="option-label">${option.label}</span></p>`;
      });
      this.$dialog.alert({
        title: '题目详情',
        message: `
          <div style="text-align: left; padding: 12px 0;white-space: normal;">
            <p style="font-weight: 600; ">${question.question}</p>
             ${options}
            <p style="color: #666; font-size: 18px;color: #ef4444;">正确答案：${this.formatAnswer(question)}</p>
            ${question.explanation ? `<p style="color: #666; font-size: 18px; line-height: 1.5;color: #ef4444;">解析：${question.explanation}</p>` : ''}
          </div>
        `,
        allowHtml: true,
      });
    },
    startPractice() {
      this.loadWrongQuestionsForPractice();
      this.$router.push({ name: 'Quiz' });
    },
  },
};
</script>

<style lang="scss" scoped>
.wrong-questions-page {
  max-width: 640px;
  margin: 0 auto;
}

.wrong-summary {
  margin-bottom: 16px;
}

.summary-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.summary-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.summary-value {
  margin: 0;
  font-size: 28px;
  font-weight: 700;
  color: #ef4444;
}

.summary-label {
  margin: 0;
  font-size: 13px;
  color: #6b7280;
}

.wrong-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.wrong-item {
  cursor: pointer;
  transition: all 0.2s ease;
}

.wrong-item:hover {
  box-shadow: 0 8px 24px rgba(239, 68, 68, 0.15);
  transform: translateY(-2px);
}

.wrong-item__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.wrong-item__id {
  font-size: 12px;
  color: #94a3b8;
}

.wrong-item__question {
  margin: 0 0 8px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
  color: #0f172a;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.wrong-item__meta {
  display: flex;
  align-items: center;
  gap: 8px;
}

.wrong-item__answer {
  font-size: 13px;
  color: #ef4444;
  font-weight: 500;
}

.empty {
  margin-top: 24px;
}
</style>

